@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
* {
  font-family: "Roboto Mono", monospace;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --red: #c10000;
  --teal: #00989d;
  --pink: #ff65c3;
  --dark-red: #7c1a0f;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
nav {
  margin: 0;
  padding: 0;
}


.navigation-bar {
  padding: 1% 2%;
  position: fixed;
  width: 100vw;
  top: 0;
  z-index: 10;
  background-color: white;
}

.navigation-bar.colourChange {
  background-color: #f8f9fa;
  box-shadow: 0px 15px 10px -15px rgb(134, 134, 134);
}

.navbar-toggler {
  color: rgba(0, 0, 0, 0.55);
}

.dropdown-nav {
  justify-content: flex-end;
}

.logo {
  max-height: 5rem;
}

.nav-links {
  display: flex;
}

.my-link {
  font-size: 1.2rem;
  display: flex;
  justify-content: flex-end;
  text-decoration: none;
  color: rgba(0, 0, 0, 0.55);
  margin: 0 1rem;
}

.my-link:hover {
  border-bottom: 2px solid;
  color: var(--red) !important;
  cursor: pointer;
}

.my-link:focus {
  border-bottom: 2px solid;
  color: var(--red) !important;
}

.active {
  border-bottom: 2px solid;
  color: var(--red) !important;
}

.hero {
  height: 80vh;
  width: 100%;
  background: url(/latinmotion/static/media/latinmotion-banner-red.285bddb0.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  margin-top: 7rem;
}

@media screen and (max-width: 768px) {
  .hero {
    height: 50vh;
    background: url(/latinmotion/static/media/saturday-open.8e853f8c.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    text-align: center;
    margin-top: 7rem;
  }
}

.section-container {
  margin: 0rem auto;
  box-sizing: border-box;
}

.about-content {
  text-align: center;
}

.about-text {
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.55);
}

.about-img {
  height: 14rem;
  width: 20rem;
}

.carousel-div {
  margin: 2rem 0;
}

@media screen and (min-width: 1400px) {
  .section-container {
    width: 70%;
  }
}

@media screen and (min-width: 768px) {
  .about-text {
    padding: 1rem 0 0 2rem;
  }
}

@media screen and (max-width: 767px) {
  .about-text {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem;
    text-align: center;
  }

  .about-img {
    height: 20rem;
    width: 25rem;
  }
}

@media screen and (max-width: 464px) {
  .about-img {
    width: 30rem;
  }
}

.section-title {
  font-size: 2.5rem;
  text-align: center;
  font-weight: bold;
  margin: 2rem 0rem;
  color: rgba(0, 0, 0, 0.55);
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  max-width: 100vw;
}

.flex-container--padded {
  padding-top: 2rem;
}

.flex-container--centered {
  justify-content: center;
}

.flex-container--grey {
  background-color: #f7f7f7;
}

@media screen and (min-width: 1000px) {
  .flex-container {
    padding-bottom: 2rem;
  }
}

.class-card {
  width: 20rem;
  height: 26rem;
  text-align: center;
  text-transform: uppercase;
  margin: 0 1rem;
  margin-bottom: 2rem;
  position: relative;
  text-decoration: none;
  color: black;
  border-radius: 10%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
}

.class-card:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15), 0 4px 8px rgba(0, 0, 0, 0.1);
}

.class-image {
  height: 20rem;
  width: 20rem;
  border-top-left-radius: 10%;
  border-top-right-radius: 10%;
}

.card-body {
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.class-book-btn {
  padding: 1rem;
  background-color: var(--teal);
  border: none;
  border-radius: 10px;
  color: white;
  font-weight: bold;
}

.class-book-btn:hover {
  background-color: var(--teal);
  color: white;
  /* box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06); */
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.logo-container {
  position: absolute;
  right: 0;
  -webkit-transform: translate(-40%);
          transform: translate(-40%);
}

.logo-container:hover {
  cursor: pointer;
}

.fbook-logo:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media screen and (min-width: 1700px) {
  .class-card {
    margin: 0 1.5rem;
    margin-bottom: 2rem;
  }
}

@media screen and (max-width: 799px) {
  .class-card {
    margin: 1 1.5rem;
    width: 18rem;
  }

  .class-image {
    height: 20rem;
    width: 18rem;
  }
}

@media screen and (max-width: 735px) {
  .class-card {
    margin: 1rem;
    width: 16rem;
  }

  .class-image {
    height: 20rem;
    width: 16rem;
  }
}

@media screen and (max-width: 623px) {
  .class-card {
    margin: 1rem;
    width: 15rem;
  }

  .class-image {
    height: 20rem;
    width: 15rem;
  }
}

@media screen and (max-width: 480px) {
  .class-card {
    margin: 1 1.5rem;
    width: 18rem;
  }

  .class-image {
    height: 20rem;
    width: 18rem;
  }
}

.classes-container {
  margin: 0rem auto;
  box-sizing: border-box;
  width: 100%;
}

.venue-cards-container {
  margin: 3rem;
  margin-top: 4rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.class-cards-container {
  margin: 3rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.timetable {
  display: flex;
  justify-content: center;
  margin-top: 4rem;
}

.venue-card {
  width: 25vw;
  height: 15rem;
  padding: 2rem;
  background-color: var(--teal);
  margin: 0 2rem;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.venue-card-small {
  border: none;
  min-width: 22vw;
  max-width: 25vw;
  height: 4rem;
  padding: 0.5rem;
  background-color: var(--teal);
  margin: 0 1rem 1rem 1rem;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.venue-name {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.venue-name-small {
  font-size: 1.2rem;
  font-weight: bold;
  color: white;
}

.venue-card:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.2);
}

.venue-card-small:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.view-btn {
  background-color: transparent;
  border: 5px solid white;
  color: white;
  width: 50%;
  height: 3rem;
}

.view-btn:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: white;
  color: var(--teal);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3), 0 4px 8px rgba(0, 0, 0, 0.2);
}

.venue-buttons-container {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 1rem 0;
}

.displayed {
  display: flex;
}

.hidden {
  display: none;
}

.modal-image {
  width: 100%;
}

@media screen and (min-width: 1700px) {
  .class-cards-container {
    margin: 3rem 15rem;
  }
}

@media screen and (max-width: 1200px) {
  .venue-card {
    width: 25vw;
    margin: 0 1rem;
    padding: 1.2rem;
  }

  .venue-name {
    font-size: 1.3rem;
  }

  .view-btn {
    width: 70%;
    height: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .venue-cards-container {
    margin: 1rem;
    margin-top: 4rem;
  }

  .class-cards-container {
    margin: 1.5rem;
  }

  .venue-card {
    width: 27vw;
    margin: 0 0.5rem;
  }

  .view-btn {
    width: 90%;
    height: 3.5rem;
  }
}

@media screen and (max-width: 600px) {
  .timetable {
    margin-top: 2rem;
  }

  .timetable > .venue-card-small {
    min-width: 80vw;
    max-width: 80vw;
    background-color: var(--red);
  }

  .venue-card-small {
    min-width: 28vw;
    max-width: 28vw;
    height: 4rem;
    padding: 0rem;
    margin: 0 0.3rem 1rem 0.3rem;
  }

  .venue-name-small {
    font-size: 1rem;
  }

  .venue-cards-container {
    margin: 1rem;
  }

  .venue-card {
    width: 90vw;
    height: 5rem;
    margin: 0.5rem 0;
  }

  .view-btn {
    width: 50%;
    height: 3.5rem;
  }
}

@media screen and (max-width: 340px) {
  .venue-card-small {
    min-width: 29vw;
    max-width: 29vw;
  }

  .venue-name-small {
    font-size: 0.8rem;
  }
}

.filter-strip-container {
  width: 100%;
  height: 4rem;
  background-color: rgba(167, 27, 27, 0.37);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 20px;
  padding: 0 2rem;
}

.filter-btn {
  background-color: white;
  color: var(--red);
  font-weight: bold;
  padding: 1rem;
}

.submit-btn-div {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
}

.apply-filter-btn {
  background-color: var(--teal);
  width: 100%;
  color: white;
  font-weight: bold;
}

.event-cards-container {
  height: 20rem;
}

.footer div {
  opacity: 0.3;
}

.portfolio-link {
  text-decoration: none;
  color: var(--red);
}

.portfolio-link:hover {
  cursor: pointer;
}

